.header {
  position: relative;
  margin-bottom: rem(80); }

.header--mini {
  position: fixed;
  z-index: 10;
  top: 0;
  left: 0;
  right: 0;
  min-width: $min-width;
  background: $color-text-main;
  color: white;
  transform: translateY(-100%);
  transition: transform 0.35s ease;
  .header__left,
  .header__login-group {
    display: flex;
    align-items: center; }
  .header__content {
    min-height: rem(30);
    padding-top: 0;
    align-items: center; }
  .header__btn-separator {
    margin: 0 rem(4); }
  &.is-visible {
    transform: none; } }

.header__bar {
  position: fixed;
  background: $color-text-main;
  height: rem(8);
  top: 0;
  right: 0;
  left: $outer-padding; }

.header__content {
  display: flex;
  justify-content: space-between;
  padding-top: rem(30); }

.header__logo {
  display: inline-block;
  @include f(m-semibold, 10, 15, 500);
  text-transform: uppercase;
  color: inherit;
  text-decoration: none; }

.header__logo-second-line {
  display: block;
  margin-top: rem(3);
  @include f(p-regular, 12, 15);
  color: $color-text-grey; }

.header__text {
  margin-top: rem(30);
  font-size: rem(14);
  line-height: (20/14); }

//.header__login-group

.header__btn-separator {
  display: inline-block;
  vertical-align: middle;
  position: relative;
  border-top: rem(1) solid;
  width: rem(50); }

.mini-logo {
  display: inline-flex;
  align-items: center;
  vertical-align: middle;
  text-decoration: none;
  color: inherit;
  @include f(m-semibold, 10, 10, 500); }

.mini-logo__line {
  margin: 0 8px;
  display: inline-block;
  vertical-align: middle;
  position: relative;
  border-top: rem(1) solid;
  width: rem(50); }

.mini-logo-sub {
  display: inline-block;
  margin-left: rem(20);
  font-size: rem(12);
  color: $color-text-grey; }
