.hub-link {
  display: block;
  position: relative;
  color: inherit;
  text-decoration: none;
  > .h1-like {
    display: block;
    vertical-align: top;
    margin-bottom: rem(10);
    max-width: rem(500); }
  .subtitle {
    position: relative;
    z-index: 1;
    display: inline;
    background: white;
    box-shadow: rem(25) 0 0 0 white;
    transition: color 0.7s ease; }
  &.is-ready {
    > .h1-like {
      visibility: hidden; } }
  &:hover {
    .hub-link__line {
      color: $color-gradient; }
    .subtitle {
      color: $color-gradient-darker; } } }

.hub-link__line {
  position: absolute;
  z-index: 0;
  bottom: 0.4em;
  right: 0;
  left: 0;
  @include f(m-regular, 10, 10, 250);
  text-transform: uppercase;
  text-align: right;
  padding-right: rem(130);
  transition: color 0.5s ease;
  .arrow-line {
    position: absolute;
    z-index: -1;
    top: 50%;
    left: 0;
    right: 0;
    margin-top: -1px; }
